import React from "react";
import { graphql } from 'gatsby';
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { FaChevronLeft } from 'react-icons/fa';

import Page from "../../components/layout/page";
import { LayoutContainer, LayoutItem, LayoutItemWrapper, LayoutRow } from "../../components/layout/layout";
import IconLink from "../../components/base/links/icon-link";
import { Heading, Text } from "../../components/base/typography";
import MarkdownContent from "../../components/markdown/markdown-wrapper";
import StickyHeader from "../../components/layout/sticky-header";

export default function BlogDetail({ data }) {
  const headerImage = getImage(data.strapiPost.cover.localFile.childImageSharp.gatsbyImageData)
  return (
    <>
      <Page
        active="blog"
        title={data.strapiPost.SEO.metaTitle}
        description={data.strapiPost.SEO.metaDescription}
        image={data.strapiPost.cover.url}
      >
        <LayoutContainer>
          <LayoutRow>
            <LayoutItemWrapper
              rowCenter
              noTopPadding
            >
              <LayoutItem
                fullWidth
                maxWidth={'40rem'}
              >
                <StickyHeader>
                  <IconLink to="/blog">
                    <FaChevronLeft />
                    Back to blog posts
                  </IconLink>
                </StickyHeader>
                <GatsbyImage
                  image={headerImage}
                  alt={data.strapiPost.cover.alternativeText}
                />
                <Heading
                  level={2}
                  marginTop
                >
                  {data.strapiPost.title}
                </Heading>
                <Text
                  level={4}
                  noTopMargin
                >
                  Published at <time dateTime={data.strapiPost.createdAt}>{data.strapiPost.fomattedCreatedAt}</time>
                </Text>
                <MarkdownContent
                  htmlAst={data.strapiPost.content.data.childMarkdownRemark.htmlAst}
                  options={{
                    p: {
                      topMargin: false
                    }
                  }
                  }

                />
              </LayoutItem>
            </LayoutItemWrapper>
          </LayoutRow>
        </LayoutContainer>
      </Page>
    </>
  )
};

export const query = graphql`
  query getBlogPost($slug: String) {
    strapiPost(slug: {eq: $slug}) {
      title     
      createdAt
      fomattedCreatedAt: createdAt(formatString: "DD-MM-YYYY")
      cover {
        alternativeText
        url
        localFile {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
      content {
        data {
          childMarkdownRemark {
            htmlAst
          }
          content
        }
      }
      SEO {
        keywords
        metaDescription
        metaTitle
      }
    }
  }
`